/* Overwrite Bootstrap style */
body {
  padding-top: 70px;
}
/* Shift down to make space for navbar */
body.modal-open,
.modal-open .navbar-fixed-top {
  margin-right: 0px;
}
/* Fix strange right margin when opening modal dialog */
.navbar-brand > img {
  display: inline-block;
}
/* Redooor style */
body {
  padding-bottom: 70px;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
header,
#header {
  margin: 0.5em 0;
}
footer,
#footer {
  padding: 2em 0;
  color: #999999;
}
/*
 * Main content
 */
.main {
  padding: 20px;
}
@media (min-width: 768px) {
  .main {
    padding-right: 0px;
    padding-left: 5px;
  }
}
.main .page-header {
  margin-top: 0;
}
/*
 * Sidebar
 */
/* Hide for mobile, show later */
.sidebar {
  display: none;
}
@media (min-width: 768px) {
  .sidebar {
    position: fixed;
    top: 64px;
    bottom: 0;
    left: 0;
    z-index: 1000;
    display: block;
    padding: 20px;
    overflow-x: hidden;
    overflow-y: auto;
    /* Scrollable contents if viewport is shorter than content. */
    background-color: #f5f5f5;
    border-right: 1px solid #eee;
  }
}
/* Sidebar navigation */
.nav-sidebar {
  margin-right: -21px;
  /* 20px padding + 1px border */
  margin-bottom: 20px;
  margin-left: -20px;
}
.nav-sidebar > li > a {
  padding-right: 20px;
  padding-left: 20px;
}
.nav-sidebar > .active > a,
.nav-sidebar > .active > a:hover,
.nav-sidebar > .active > a:focus {
  color: #fff;
  background-color: #428bca;
}
/* Block componenets */
.block-header {
  display: inline-block;
}
#nav-bottom-phone {
  margin: 0;
}
.contact-us {
  text-align: right;
}
.contact-us h4 {
  margin-right: 0.4em;
}
.nav-controls {
  padding-bottom: 0.5em;
}
.official-color {
  color: #ed1b33;
}
.redooor-nav-logo {
  color: #333333;
}
.redooor-nav-logo:hover {
  color: #333333;
  text-decoration: none;
}
.redooor-nav-logo img {
  height: 60px;
}
.redooor-nav-logo .slogan {
  font-size: 1.2em;
}
.redooor-nav-logo {
  height: 18px;
  vertical-align: text-top;
}
.navbar-menu-current {
  color: #999 !important;
}
.hero-text {
  font-size: 3.5em;
  line-height: 1.4em;
}
.hero-strip {
  margin: 2em 0 4em 0;
  padding: 5em 2em;
  color: white;
  background-color: gray;
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-box-shadow: 0 0 40px rgba(0, 0, 0, 0.3) inset;
  -moz-box-shadow: 0 0 40px rgba(0, 0, 0, 0.3) inset;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.3) inset;
}
/* Faded out hr */
hr.soften {
  height: 1px;
  margin: 70px 0;
  background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0));
  background-image: -moz-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0));
  background-image: -ms-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0));
  background-image: -o-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0));
  border: 0;
}
.box {
  display: block;
  padding: 1em;
  line-height: 20px;
  position: relative;
  border: 1px solid #efefef;
  background: #fff;
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.06) inset;
  -moz-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.06) inset;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.06) inset;
}
.box:before,
.box:after {
  content: '';
  z-index: -1;
  position: absolute;
  left: 10px;
  bottom: 1em;
  width: 70%;
  max-width: 300px;
  /* avoid rotation causing ugly appearance at large container widths */
  max-height: 100px;
  height: 55%;
  -webkit-box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  -webkit-transform: skew(-15deg) rotate(-6deg);
  -moz-transform: skew(-15deg) rotate(-6deg);
  -ms-transform: skew(-15deg) rotate(-6deg);
  -o-transform: skew(-15deg) rotate(-6deg);
  transform: skew(-15deg) rotate(-6deg);
}
.box:after {
  left: auto;
  right: 10px;
  -webkit-transform: skew(15deg) rotate(6deg);
  -moz-transform: skew(15deg) rotate(6deg);
  -ms-transform: skew(15deg) rotate(6deg);
  -o-transform: skew(15deg) rotate(6deg);
  transform: skew(15deg) rotate(6deg);
}
.box:hover {
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.09) inset;
  -moz-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.09) inset;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.09) inset;
}
/* For sign in page */
.form-signin {
  background-color: #eee;
  border: 1px solid #e5e5e5;
  padding-bottom: 15px;
}
i {
  margin-right: 8px;
  -webkit-print-color-adjust: exact;
}
.icon-phone {
  background-image: url("../img/glyphicons_169_phone.png");
  background-position: 2px 0;
  background-size: contain;
}
.icon-twitter {
  background-image: url("../img/glyphicons_411_twitter.png");
  background-position: 0 0;
  background-size: contain;
}
ul.redooor-hierarchy {
  padding: 0;
  margin: 0;
  list-style: none;
  max-height: 200px;
  overflow-y: scroll;
}
ul.redooor-hierarchy ul {
  padding-left: 1.2em;
  list-style: none;
}
ul.redooor-hierarchy li {
  line-height: 1.8;
}
ul.redooor-hierarchy a {
  padding: 0.3em 0.5em;
}
ul.redooor-hierarchy a.active {
  color: #fff;
  background: #428bca;
}
ul.redooor-hierarchy.no-max-height {
  max-height: none;
}
.rdpt-preloader {
  text-align: center;
  padding: 1em;
  display: none;
}
/* twitter typeahead */
.twitter-typeahead {
  width: 100%;
}
.scrollable-dropdown-menu .tt-dropdown-menu {
  max-height: 150px;
  overflow-y: auto;
  background-color: #ffffff;
  border: 1px solid #dddddd;
}
.tt-dropdown-menu .tt-suggestion {
  cursor: pointer;
  padding: 0.5em 1em;
}
.tt-dropdown-menu .tt-suggestion:hover {
  color: white;
  background-color: #428bca;
}
.tt-dropdown-menu .tt-suggestion p {
  padding: 0;
  margin: 0;
}
/* Overwrite default padding on small screens */
@media (max-width: 979px) {
  .hero-text {
    font-size: 2.8em;
  }
  h1 {
    font-size: 30px;
  }
  h1 small {
    font-size: 20px;
  }
  h2 {
    font-size: 24px;
  }
  .card {
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  body {
    padding-left: 0;
    padding-right: 0;
  }
  #header,
  #main,
  #footer {
    padding-left: 20px;
    padding-right: 20px;
  }
  .hero-text {
    font-size: 2.5em;
  }
}
@media (max-width: 480px) {
  h1 {
    font-size: 24px;
  }
  h1 small {
    font-size: 16px;
  }
  .hero-text {
    font-size: 2em;
  }
}
.panel.panel-card .panel-heading {
  min-height: 80px;
  position: relative;
  margin-bottom: 27px;
}
.panel.panel-card .panel-heading .panel-title {
  font-size: 24px;
}
.panel.panel-card .panel-heading .btn-action {
  position: absolute;
  bottom: -27px;
  right: 1.5em;
}
.btn.disabled.btn-text {
  background-color: #eee;
  color: #333;
}
